module.exports = {
  // 
  val_empty(v, txt) {
    if ((v == null) || (v.trim() == '')) {
      return txt + ''
    }
    return true
  },
  val_char(v, txt) {
    let char = /[`~!@#$%^&*('_+|=;.\\)<>?:"{},.\/;'[\]]/;
    if (char.test(v)) {
      return txt + ''
    }
    return true
  },
  val_char_price(v, txt) {
    let char = /[`~!@#$%^&*('_+|=;\\)<>?:"{},\/;'[\]]/;
    if (char.test(v)) {
      return txt + '.'
    }
    return true
  },
  val_email(v, txt) {
    let char = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
    if (!char.test(v)) {
      return txt + ''
    }
    return true
  },
  val_length(v, txt, min, max) {
    if (v.length < min) {
      return txt + '' + min
    }
    if (v.length > max) {
      return txt + '' + max
    }
    return true
  },
  val_timed(v, txt, short) {
    if (short) {
      if ((v.length > 10) && (v.length < 8)) {
        return txt + ''
      }
      let char = /[`~!@#$%^&*('_+|=;\\)<>?:"{},.\/;'[\]]/;
      if (char.test(v)) {
        return txt + ' - '
      }
    }
    return true
  },
  val_phoned(phone, txt) {
    if (!phone | phone == undefined | phone == '') {
        return ''
    } else {
        const len = phone.length
        let char = /[`~!@#$%^&*('_+|=;\\)<>?:"{},.\/;'[\]]/;

        if (isNaN(phone)) { return '' }
        if (char.test(phone)) {
            return ''
        }
        char = /[abcdefghijklmopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ]/;
        if (char.test(phone)) {
            return 'a-zA-Z'
        }
        if (len != 8) {
            return '8'
        }
    }
    return true
  }
}


